<template>
  <div class="idms-alarm-notifi w-100 d-flex flex-row justify-content-end px-3 py-3">
    <el-badge :hidden="n_count==0" type="danger" :value="n_count">
      <el-button size="large" circle @click="show_form=!show_form">
        <el-icon size="40px">
          <WarningFilled />
        </el-icon>
      </el-button>
    </el-badge>
    <el-drawer direction="ltr" size="80%" v-model="show_form" :show-close="false">
      <!-- Header -->
      <template #header="{   titleId }">
        <div :id="titleId" class="drawer-title d-flex flex-row border-bottom">
          <div class="flex-fill text-start">
            <h4 v-text="title"></h4>
          </div>
          <div>
            <el-button icon="close" circle type="danger"></el-button>
          </div>
        </div>
      </template>
      <div class="my-0 d-flex flex-column h-100">
        <div class="flex-fill h-100">
          <AlarmTableVue @new_alarm="NewAlarmHandle" :edge_ip="edge_ip"></AlarmTableVue>
        </div>
        <!-- foot -->
        <div class="text-start px-2 border-top">{{edge_ip}}</div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import AlarmTableVue from './AlarmTable.vue';
export default {
  components: {
    AlarmTableVue,
  },
  props: {
    edge_ip: {
      type: String,
      default: "127.0.0.1"
    },
  },
  data() {
    return {
      title: "Alarm Form",
      n_count: 0,
      show_form: false
    }
  },
  methods: {
    NewAlarmHandle(alarm) {
      console.info(alarm);
      this.n_count = this.n_count + 1;
    }
  },
  mounted() {
  }
}
</script>

<style>
.idms-alarm-notifi .drawer-title {
  padding-top: 80px;
}
</style>